import React, { useEffect, useState } from 'react'
import Axios from 'axios';
import Swal from 'sweetalert2'
import { useHistory,Redirect } from "react-router-dom";
import Navigation from './Navigation';

function CustomerSignin(){

    const [data,setData] = useState({
        accountNumber:'',
        password:''
    });

    const handleInput = (e)=>{
        setData({...data,[e.target.name]:e.target.value})
    }

    const history = useHistory();

    const login = async()=>{
        try{
            const credentials = {
                account_number:data.accountNumber,
                password:data.password
            };

            console.log(credentials);

            const response = await Axios.get(`http://localhost:8000/api/customers`,{params: credentials});

            if (response.status===200){
                Swal.fire({
                    title: 'Success',
                    text: `${response.data.message}`,
                    icon: 'success',
                    confirmButtonText: 'Ok'
                });
                history.push('/customer/home');
                // return <Redirect to="/customer/home" />
            }
            
        }catch(error){
            console.log(error);
            if (error.response.status===404){
                Swal.fire({
                    title: 'Not found!',
                    text: `${error.response.data.message}`,
                    icon: 'question',
                    confirmButtonText: 'Ok'
                });
            }else if (error.response.status===401){
                Swal.fire({
                    title: 'Invalid!',
                    text: `${error.response.data.message}`,
                    icon: 'error',
                    confirmButtonText: 'Ok'
                });
            }
            
        }
    }


    return (
        <div className="" style={{height:'100vh'}}>
          <Navigation></Navigation>
          <div className="container mt-5">
            <div className="row">
              <div className="col-md-6 d-flex flex-column align-items-center justify-content-center">
                <span className="fs-1" style={{fontSize:'10px'}}>Elctricity Bill Management System</span>
                <h1 style={{fontSize:'60px'}}>WELCOME</h1>
                <p>Easiest way to keep meter reading records</p>
                <div className="d-flex justify-content-center gap-4">
                </div>
              </div>
                <div className="col-md-6 pt-1">
                <div className="d-flex justify-content-center align-items-center">
            <div class="card w-75 mb-3 border-5 mt-5">
                <div class="card-body p-5 ">
                    <h5 class="card-title text-center fw-bold">Sign In</h5>

                    <label>Account Number</label>
                    <input type="text" className="form-control mb-2" name='accountNumber' value={data.accountNumber} onChange={handleInput}></input>

                    <label>Password</label>
                    <input type="password" className="form-control mb-2" name='password' value={data.password} onChange={handleInput}></input>

                    <div className="d-flex justify-content-center align-items-center mb-3">
                        <button class="btn btn-primary" onClick={login}>Sign In</button>
                    </div>
                    <label>Don't have an account ?<a href="/customer/signup" className="ms-2">Register</a></label>
                </div>
            </div>
        </div>
                </div>
              
            </div>
          </div>
      </div>
    );
}

export default CustomerSignin;