import Axios  from 'axios';
import React, { useState } from 'react'
import Navigation from './Navigation'
import Swal from 'sweetalert2'

function AdminHome() {

  const [accountNumber,setAccountNumber] = useState(''); 
  const [date,setDate] = useState(''); 
  const [reading,setReading] = useState(''); 
  const [errors,setErrors]=useState([]);

  const handlePostClick = async()=>{
    try{
      const readingValue = {
        customer_account_number:accountNumber,
        reading_date:date,
        value:reading,
        month:+date.substring(5,7)
      }

      // console.log(readingValue);

      const response = await Axios.post('http://localhost:8000/api/readings',readingValue);

      if (response.status===201){
        Swal.fire({
            title: 'Success!',
            text: 'Reading Successfully saved!',
            icon: 'success',
            confirmButtonText: 'Ok'
        })
        setAccountNumber('');
        setDate('');
        setReading('');
        setErrors([]);
        
      }else{
        console.error('Error:',response.data.message);
      }
    }catch(error){
      console.log(error);
      if (error.response.status===422){
        if (error.response.data.code===1){
          setErrors(error.response.data.errors);
        }else if (error.response.data.code===2){
          Swal.fire({
            title: 'Error!',
            text: `${error.response.data.message}`,
            icon: 'error',
            confirmButtonText: 'Ok'
          })
        }
      }else{
        Swal.fire({
          title: 'Error!',
          text: `${error.response.data.message}`,
          icon: 'error',
          confirmButtonText: 'Ok'
        })
        setErrors([]);
      }
    }
  }

  return (
    <div>
      <Navigation></Navigation>
      <div className="d-flex justify-content-center align-items-center" style={{height:'80vh',width:'100vw'}}>
            <div class=" mb-3 border-5" style={{width:'30%'}}>
                <div class="card-body p-5" style={{minWidth:'200px'}}>
                    <h2 class="card-title text-center fw-bold mb-4">Enter New Reading</h2>
                    <div className='mb-3'>
                      <label className='form-label'>Account Number</label>
                      <input placeholder='Enter account number: Ex:10000001' type="text" className="form-control mb-1 p-3"  value={accountNumber} onChange={(e)=>setAccountNumber(e.target.value)}></input>
                      <span className='text-danger fw-bold'>{errors.customer_account_number}</span>
                    </div>
                    <div className='mb-3'>
                      <label className='form-label'>Date</label>
                      <input type="date" className="form-control mb-1 p-3" value={date} onChange={(e)=>setDate(e.target.value)}></input>
                      <span className='text-danger fw-bold'>{errors.reading_date}</span>
                    </div>
                    <div className='mb-3'>
                      <label className='form-label'>Reading</label>
                      <input placeholder='Enter the reading' type="number" className="form-control mb-1 p-3" value={reading} onChange={(e)=>setReading(e.target.value)}></input>
                      <span className='text-danger fw-bold'>{errors.value}</span>
                    </div>
                    <div className="d-flex justify-content-center align-items-center mb-3">
                        <a href="#" class="btn btn-primary" onClick={handlePostClick}>Save </a>
                    </div>
                </div>
            </div>
      </div>
    </div>
  )
}

export default AdminHome