import { useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import Axios  from "axios";
import Swal from 'sweetalert2'
import {ValidationCustomerRegistration} from '../common/Validation.js'
import Navigation from "./Navigation.js";

function CustomerSignup(){

    const [data,setData] = useState({
        name:'',
        accountNumber:'',
        email:'',
        password:'',
        confirmPassword:''
    });

    const [errors,setErrors] = useState({});

    const handleInput = (e)=>{
        setData({...data,[e.target.name]:e.target.value})
    }


    function handleValidation(e){
        setErrors(ValidationCustomerRegistration(data))
    }
    
    const valid = errors.valid;
    // console.log(valid);

    const saveData = async()=>{
        try{
            const customerData = {
                name:data.name,
                account_number:data.accountNumber,
                email:data.email,
                password:data.password
            };

            handleValidation();
            console.log(valid);
            
            if (!valid){
                // console.log("invalid");
                return;
            }

            const response = await Axios.put('http://localhost:8000/api/customers',customerData);

            if (response.status===201){
                Swal.fire({
                    title: 'Success!',
                    text: `${response.data.message}`,
                    icon: 'success',
                    confirmButtonText: 'Ok'
                });
                setData({});
                
            }else{
                console.error('Error:',response.data.message)
            }

        }catch (error){
            if (error.response.status===422){
                Swal.fire({
                    title: 'Error!',
                    text: 'Invalid input. please insert correct data',
                    icon: 'question',
                    confirmButtonText: 'Ok'
                });
            }else if(error.response.status===404){
                Swal.fire({
                    title: 'Error!',
                    text: `${error.response.data.message}`,
                    icon: 'error',
                    confirmButtonText: 'Ok'
                });
            }
            console.log('Error',error);
        }
    };



    return (
        <div className="" style={{height:'100vh'}}>
          <Navigation></Navigation>
          <div className="container mt-5">
            <div className="row">
              <div className="col-md-6 d-flex flex-column align-items-center justify-content-center">
                <span className="fs-1" style={{fontSize:'10px'}}>Elctricity Bill Management System</span>
                <h1 style={{fontSize:'60px'}}>WELCOME</h1>
                <p>Easiest way to keep meter reading records</p>
                <div className="d-flex justify-content-center gap-4">
                </div>
              </div>
                <div className="col-md-6 pt-1">
                <div className="d-flex justify-content-center align-items-center">
            <div class="card w-75 mb-3 border-5">
                <div class="card-body p-5">
                    <h5 class="card-title text-center fw-bold">Register</h5>
                    <label>Name</label>
                    <input type="text" className="form-control mb-2" name="name" value={data.name} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.name}</p>}

                    <label>Account Number</label>
                    <input type="text" className="form-control mb-2" name="accountNumber" value={data.accountNumber} onChange={handleInput}></input>
                    {errors.accountNumber && <p className="small-font" style={{color: "red"}}>{errors.accountNumber}</p>}

                    <label>Email</label>
                    <input type="email" className="form-control mb-2" name="email" value={data.email} onChange={handleInput}></input>
                    {errors.email && <p className="small-font" style={{color: "red"}}>{errors.email}</p>}

                    <label>Password</label>
                    <input type="password" className="form-control mb-2" name="password" value={data.password} onChange={handleInput}></input>
                    {errors.password && <p className="small-font" style={{color: "red"}}>{errors.password}</p>}

                    <label>Confirm Password</label>
                    <input type="password" className="form-control mb-2" name="confirmPassword" value={data.confirmPassword} onChange={handleInput}></input>
                    {errors.confirmPassword && <p className="small-font" style={{color: "red"}}>{errors.confirmPassword}</p>}

                    <div className="d-flex justify-content-center align-items-center mb-3">
                        <a href="#" class="btn btn-primary" onClick={saveData}>Register</a>
                    </div>
                    <label>Already have an account?<Link to="/customer/signin" className="">Sign in</Link></label>
                </div>
            </div>
        </div>
                </div>
              
            </div>
          </div>
      </div>
    );
}

export default CustomerSignup;