import Axios from "axios";
import { useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import Swal from 'sweetalert2';
import {ValidationReaderRegistration} from '../common/Validation.js'
import Navigation from "./Navigation.js";


function AdminSignUp(){

    const [data,setData] = useState({
        name:'',
        contact:'',
        email:'',
        password:'',
        confirmPassword:''
    });

    const [errors,setErrors] = useState({});

    const handleInput = (e)=>{
        setData({...data,[e.target.name]:e.target.value})
    }

    function handleValidation(e){
        setErrors(ValidationReaderRegistration(data))
    }

    const valid = errors.valid;
    // console.log(errors.valid);

    const saveData = async()=>{
        try{
            const readerData = {
                name:data.name,
                employee_no:data.employeeNumber,
                email:data.email,
                password:data.password
            };

            handleValidation();

            const response = await Axios.post('http://localhost:8000/api/readers',readerData);

            if (response.status===201){
                    Swal.fire({
                    title: 'Success!',
                    text: 'Registration Successfull!',
                    icon: 'success',
                    confirmButtonText: 'Ok'
                    });
            }else{
                console.error('Error:',response.data.message)
            }
            

        }catch (error){
            console.log('Error',error.message);
            if (error.response.status===422){
                Swal.fire({
                    title: 'Error!',
                    text: 'Invalid input. please insert correct data',
                    icon: 'question',
                    confirmButtonText: 'Ok'
                });
            }else{
                Swal.fire({
                    title: 'Error!',
                    text: `${error.response.data.message}`,
                    icon: 'error',
                    confirmButtonText: 'Ok'
                });
            }
        }
    };

    return (
        <div className="" style={{height:'100vh'}}>
        <Navigation></Navigation>
        <div className="container mt-5">
          <div className="row">
            <div className="col-md-6 d-flex flex-column align-items-center justify-content-center">
              <span className="fs-1" style={{fontSize:'10px'}}>Elctricity Bill Management System</span>
              <h1 style={{fontSize:'60px'}}>WELCOME</h1>
              <p style={{fontSize:'20px'}}>Easiest way to keep meter reading records</p>
              <div className="d-flex justify-content-center gap-4">
              </div>
            </div>
            
              <div className="col-md-6 pt-1">
              <div className="d-flex justify-content-center align-items-center">
            <div class="card w-75 mb-3 border-5">
                <div class="card-body p-5">
                    <h5 class="card-title text-center fw-bold">Register</h5>
                    <label>Name</label>
                    <input id="txtName" type="text" className="form-control mb-2" name="name" value={data.name} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.name}</p>}

                    <label>Employee Number</label>
                    <input id="txtEmployeeNumber" type="text" className="form-control mb-2" name="employeeNumber" value={data.employeeNumber} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.employeeNumber}</p>}

                    <label>Email</label>
                    <input id="txtEmail" type="text" className="form-control mb-2" name="email" value={data.email} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.email}</p>}

                    <label>Password</label>
                    <input id="txtPassword" type="password" className="form-control mb-2" name="password" value={data.password} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.password}</p>}

                    <label>Confirm Password</label>
                    <input id="txtConfirmPassword" type="password" className="form-control mb-2" name="confirmPassword" value={data.confirmPassword} onChange={handleInput}></input>
                    {errors.name && <p className="small-font" style={{color: "red"}}>{errors.confirmPassword}</p>}

                    <div className="d-flex justify-content-center align-items-center mb-3">
                        <a href="#" onClick={saveData} class="btn btn-primary">Register</a>
                    </div>
                    <label>Already have an account?<Link to='/reader/signin' className="ms-2">Sign in</Link></label>
                </div>
            </div>
        </div>
              </div>
            
          </div>
        </div>
    </div>
    );
}

export default AdminSignUp;